import React, { Component } from 'react';
import { Card, CardImg, CardText, CardBody, CardTitle, Breadcrumb, BreadcrumbItem, Button, Modal, ModalHeader, ModalBody, Label, Col, Row } from 'reactstrap';
import { Link } from 'react-router-dom';
import { Control, LocalForm, Errors } from 'react-redux-form';
import { Loading } from './LoadingComponent';
import { baseUrl } from '../shared/baseURL';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';

const maxLength = (length) => (value) => !(value) || (value.length <= length);
const minLength = (length) => (value) => (value) && (value.length >= length);

class CommentForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isModalOpen: false
    };
    this.toggleModal = this.toggleModal.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(values) {
    this.toggleModal();
    this.props.postComment(
      this.props.dishId, 
      values.rating, 
      values.author, 
      values.comment);
  }
  
  toggleModal() {
    this.setState({
      isModalOpen: !this.state.isModalOpen
    });
  }

  render() {
    return(
      <>
        <Button outline onClick={this.toggleModal}>
          <span className="fa fa-pencil fa-lg"> Submit Comment</span>
        </Button>
        <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
          <ModalHeader toggle={this.toggleModal}>Submit Comment</ModalHeader>
          <ModalBody>
            <LocalForm onSubmit={(values) => this.handleSubmit(values)}>
              <Row className="form-group">
                <Label htmlFor="rating" md={2}>Rating</Label>
                <Col md={10}>
                  <Control.select model=".rating" id="rating" name="rating" className="form-control">
                    <option value="1">1</option>
                    <option value="2">2</option>
                    <option value="3">3</option>
                    <option value="4">4</option>
                    <option value="5">5</option>
                  </Control.select>
                </Col>
              </Row>
              <Row className="form-group">
                <Label htmlFor="author" md={2}>Your Name</Label>
                <Col md={10}>
                  <Control.text model=".author" id="author" name="author" placeholder="Your Name" 
                  className="form-control" 
                  validators={{
                    minLength: minLength(3),
                    maxLength: maxLength(15)
                  }}/>
                  <Errors
                    className="text-danger"
                    model=".author"
                    show="touched"
                    messages={{
                      minLength: 'Must be greater than 2 characters',
                      maxLength: 'Must be 15 characters or less'
                    }}
                  />
                </Col>
              </Row>
              <Row className="form-group">
                <Label htmlFor="comment" md={2}>Comment</Label>
                <Col md={10}>
                <Control.textarea model=".comment" id="comment" name="comment" rows="6" className="form-control">
                </Control.textarea>
                </Col>
              </Row>
              <Row className="form-group">
                <Col md={{size:10, offset:2}}>
                  <Button type="submit" color="primary">
                    Send Feedback
                  </Button>
                </Col>
              </Row>
            </LocalForm>
          </ModalBody>
        </Modal>
      </>
    );
  }
}

function RenderDish({dish}) {
  return (
    <FadeTransform in 
      tranformProps={{
        exitTransform: 'scale(0.5) translateY(-50%)'
      }}>
      <Card>
        <CardImg width="100%" src={baseUrl + dish.image} alt={dish.name} />
        <CardBody>
          <CardTitle heading>{dish.name}</CardTitle>
          <CardText>{dish.description}</CardText>
        </CardBody>
      </Card>
    </FadeTransform>
  );
}

function RenderComments({comments, postComment, dishId}) {
  if (comments != null) {
    const commentList = comments.map(com => {
      return (
          <Fade in>
          <li>
            <p> {com.comment} </p>
            <p>-- {com.author}, {new Intl.DateTimeFormat('en-US', 
              { year: 'numeric', month: 'short', day: '2-digit'})
              .format(new Date(Date.parse(com.date)))}</p>
          </li>
          </Fade>
      );
    });

    return (
      <div className="text-left">
        <h4>Comments</h4>
        <ul className="list-unstyled">
          <Stagger in>
            {commentList}
          </Stagger>
        </ul>
        <div>
          <CommentForm dishId={dishId} postComment={postComment}/>
        </div>
      </div>
    );
  }
  else {
    return (
      <div>empty</div>
    );
  }
}

const DishDetail = (props) => {
  if (props.isLoading) {
    return (
      <div className="container">
        <div className="row">
          <Loading />
        </div>
      </div>
    );
  }

  else if (props.dishesErrMess) {
    return (
      <div className="container">
        <div className="row">
          <h4>{props.dishesErrMess}</h4>
        </div>
      </div>
    );
  }

  else if (props.dish != null) {
    return (
      <div className="container">
        <div className="row">
          <Breadcrumb>
            <BreadcrumbItem>
              <Link to ='/home'>Home</Link>
            </BreadcrumbItem>
            <BreadcrumbItem>
              <Link to ='/menu'>Menu</Link>
            </BreadcrumbItem>
            <BreadcrumbItem active>{props.dish.name}</BreadcrumbItem>
          </Breadcrumb>
          <div className="col-12">
            <h3>{props.dish.name}</h3>
            <hr />
          </div>
          <div className="row">
            <div className="col-12 col-md-5 m-1">
              <RenderDish dish={props.dish} />
            </div>
            <div className="col-12 col-md-5 m-1">
              <RenderComments 
                comments={props.comments} 
                postComment={props.postComment} 
                dishId={props.dish.id} />
            </div>
          </div>
        </div>
      </div>
    );
  }
  
  else {
    return (
      <div></div>
    );
  }
}


export default DishDetail;